import { stringify } from 'querystring';
import React from 'react';

import { Link } from 'react-router-dom';

import {login, signup} from '../../assets/icons';

const AuthButton = (props : any) => {

    interface Data {
        type: number;
        value: string;
        img: string;
        
        url: string;
        color: string;
    };

    const dataList : Array<Data> =  [
        {type: 0, value: '로그인', img: login, url: '/', color: '#296498'},
        {type: 1, value: '등록', img: signup, url: '/', color: '#36871A'},
        {type: 2, value: '로그아웃', img: '0', url: '/', color: '#A3BBD0'},
    ];

    let findData = dataList.find(e => e.type === Number(props.type));

  return (
    <div className='authButton' style={{backgroundColor:findData?.color}}>
        <img src={findData?.img} alt="" />
        <Link to="/">{findData?.value}</Link>
    </div>
            
  );
};

export default AuthButton;