import { stringify } from 'querystring';
import React, {useEffect} from 'react';

import { Link, useParams, useSearchParams,  useNavigate } from 'react-router-dom';

import AuthButton from './Button';
import {logo} from '../../assets/images';
import {li} from '../../assets/icons';



const NavBar = (props : any) => {

  const params = useParams();

  let NavClassName: string = 'NavWrap';

  if(!(params.subTitle === 'KOMSA' || params.subTitle === '안내' || params.subTitle === '소식')){
    NavClassName += '  NavLarge';
  }

  return (
    <div className={NavClassName}>
        <div className='Nav'>
          <div>
            <img src={logo} alt="logo" className='logo'/>
          </div>
          
          <div className='linkWrap'>
            <ul>
              <li><Link to="/">홈</Link></li>
              <img src={li} alt="" />
              <li><Link to="/KOMSA">KOMSA</Link></li>
              <img src={li} alt="" />
              <li><Link to="/안내">안내</Link></li>
              <img src={li} alt="" />
              <li><Link to="/소식">소식</Link></li>
              <img src={li} alt="" />
              <li><Link to="/설계">설계</Link></li>
              <img src={li} alt="" />
              <li><Link to="/관리">관리</Link></li>
            </ul>
          </div>

          <div className='buttonWrap'>
            <AuthButton type='0'></AuthButton>
            <AuthButton type='1'></AuthButton>
          </div>
          
        </div>
      </div>
            
  );
};

export default NavBar;