import { stringify } from 'querystring';
import React from 'react';

import { Link } from 'react-router-dom';


const NavBar = (props : any) => {


    let title: string = props.title;


    interface Menu {
        title: string;
        content: string;
    }

    let menu: Array<Menu> = [
        {title: 'KOMSA', content: 'AI 지원 어선 설계 플랫폼을 통하여\n성능 좋은 표준화 어선 제작을 지원합니다'},
        {title: '안내', content: 'AI 지원 어선 설계 플랫폼의 기술 소개와\n플랫폼 이용 안내를 제공합니다'},
        {title: '소식', content: '플랫폼을 통한 어선 설계 기술 및 정보를\n전달해 드립니다'}
    ]   


    let content = menu.find(e => e.title === title)?.content || null;

    console.log(content);

    let subTitle = [];


    if(content === null){
        
    } else {
        subTitle.push(<div className='subTitleWrap'>
                        <div className='subTitle'>
                            <h1>{title}</h1>
                            <pre>{content}</pre>
                        </div>
                    </div>);

    }



  return (
    <div>
        {subTitle}
    </div>
    
            
  );
};

export default NavBar;